@import url(https://fonts.googleapis.com/css2?family=Merriweather&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Merriweather', serif;
}
.App {
  display: -webkit-flex;
  display: flex;
}
button {
  margin: 5px;
  padding: 5px 15px 5px 15px;
  background-color: rgb(0, 0, 0);
  color: white;
  border: 1px solid black;
  border-radius: 15px;
}
.count-area {
  margin: 2rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  border: 2px solid black;
  padding: 1rem;
  max-width: 500px;;
  background-color: hsl(0, 6%, 90%);
}

.wrapper {
  -webkit-order: 1;
          order: 1;
}
.text-area {
  -webkit-order: 2;
          order: 2;
  margin: 2rem;
}

@media(max-width: 768px){
  .App {
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
  }
  .text-area {
    -webkit-order: 1;
            order: 1;
  }
  .text-area textarea {
    padding: 1rem;
    width: 100%;
  }
  .wrapper {
    -webkit-order: 2;
            order: 2;
    width: auto;
  }
}

